<template>
    <div class="boxes">
        <div  class="innovation-head">
            <div class="innovation-text">航司查询</div>
            <input class="innovation-input" type="text" v-model="selectData" @click.stop="openShow"/>
            <button class="innovation-qbuttom" @click="queryData"></button>
        </div>
        <depQuery :select="selectData" v-on:message="recieveMessage" v-if="opens"></depQuery>
        <div class="innovation-body" :class="{innovationMin:!shos}" id="airLineA" v-if="data!=''">
            <div class="innovation-box" id="airLineB">
                <div class="innovation-block">
                    <div class="innovation-minute innovation-minuteO">
                        <h4 class="innovation-blockT">基本信息</h4>
                        <ul class="innovation-block">
                            <li>
                                <div class="inn-f">航司名称：</div>
                                <div class="inn-fN">{{data.airln_Cd}}</div>
                                <div class="inn-l">IATA代码：</div>
                                <div class="inn-fN">{{data.iATA}}</div>
                            </li>
                            <li>
                                <div class="inn-f">ICAO代码：</div>
                                <div class="inn-fN">{{data.iCAO}}</div>
                                <div class="inn-l">总部位置：</div>
                                <div class="inn-fN">{{data.head_Loca}}</div>
                            </li>
                        </ul>
                    </div>
                    <div class="innovation-details nice-btn-dBlue" @click="queryMore" v-show="!shos">查看详情</div>
                    <div class="innovation-minute">
                        <ul class="innovation-block">
                            <li>
                                <div class="inn-f">发展史：</div>
                                <div class="inn-fN inn-fNs">{{data.the_History_Of}}</div>
                            </li>
                            <li>
                                <div class="inn-f">品牌定位：</div>
                                <div class="inn-fN">{{data.platformPositioning}}</div>
                            </li>
                            <li>
                                <div class="inn-f">飞机数量：</div>
                                <div class="inn-fN inn-fN1">
                                    <div>{{data.airln_number}}</div>
                                    <div>机型：</div>
                                    <div>{{data.airCrft_Typ}}</div>
                                </div>
                                <div class="inn-l">数量：</div>
                                <div class="inn-fN">{{data.number}}</div>
                            </li>
                            <li>
                                <div class="inn-f">平均机龄：</div>
                                <div class="inn-fN">{{data.avg_age}}</div>
                            </li>
                            <li>
                                <div class="inn-f">飞行员总数：</div>
                                <div class="inn-fN">{{data.man_Count}}</div>
                            </li>
                            <li>
                                <div class="inn-f">在飞航线：</div>
                                <div class="inn-fN inn-fN1">
                                    <div>{{data.airline_true}}条</div>
                                    <div>国内：</div>
                                    <div>{{data.airline_CH}}条</div>
                                </div>
                                <div class="inn-l">国际：</div>
                                <div class="inn-fN">{{data.airline_inter}}条</div>
                            </li>
                        </ul>
                    </div>
                    <div class="innovation-minute">
                        <h4 class="innovation-blockT">基地情况</h4>
                        <ul class="innovation-block">
                            <li>
                                <div class="inn-f">基地名：</div>
                                <div class="inn-fN">{{data.base_Name}}</div>
                                <div class="inn-l">基地所在城市：</div>
                                <div class="inn-fN">{{data.base_city}}</div>
                            </li>
                            <li>
                                <div class="inn-f">飞机数量：</div>
                                <div class="inn-fN">{{data.air_number}}</div>
                                <div class="inn-l">机型：</div>
                                <div class="inn-fN">{{data.air_type}}</div>
                            </li>
                            <li>
                                <div class="inn-f">过夜基地位置：</div>
                                <div class="inn-fN inn-fNs">{{data.air_location}}</div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <scrollBar3 :das="scrollNode" :switched="shos"></scrollBar3>
        </div>
    </div>
</template>
<script>
    import scrollBar from '../component/scrollBarUltimate.vue';
    import depQuery from '../template/depSearch.vue'
    export default {
        data () {
            return {
                scrollNode:{
                    parent:"airLineA",
                    child:"airLineB",
                    courseN:"course3",
                    sliderN:"slider3",
                    speed:60
                },
                shos:false,
                qShow:1,
                selectData:"",
                selectCode:"",
                opens:0,
                data:""
            }
        },
        props:['close'],
        watch:{
            close:function () {
                this.opens=0;
            }  
        },
        computed:{
            
        },
        methods:{
            recieveMessage: function (data) {
               this.selectData=data.name;
               this.selectCode=data.iT;
               this.opens=0;
            },
            queryMore:function () {
                this.shos=true;
            },
            openShow:function () {
                this.opens=1;
            },
            queryData:function (val) {
                if(this.selectCode!=""){
                    $.ajax({
                        type:'get',
                        url : 'restful/getOurBoatDetailInfoByCode',
                        data:{
                            code:this.selectCode
                        },
                        dataType : 'jsonp',
                        success:(data)=>{
                            this.data=data.success.ourBoatDetailInfo;
                        },
                    })
                }
            }
        },
        mounted(){

        },
        components:{
            scrollBar3:scrollBar,
            depQuery:depQuery
        }
    }
</script>
<style scoped>
    .inn-fNs{
        width: 716px !important;
    }
    .innovation-box{
        height: auto !important;
    }
    .innovation-minuteO{
        margin-top: 100px;
    }
    .innovationMin{
        height: 320px !important;
    }
</style>


